package dev.stralman.pages.posts.kicad

import androidx.compose.runtime.*
import com.varabyte.kobweb.core.*
import com.varabyte.kobwebx.markdown.*

@Page
@Composable
fun Dxf_edge_cut_and_zonePage() {
    CompositionLocalProvider(LocalMarkdownContext provides MarkdownContext("posts/kicad/dxf_edge_cut_and_zone.md", mapOf("title" to listOf("Edge cut and copper zone from CAD sketch"), "date" to listOf("2024-06-24"), "author" to listOf("Karl Strålman"), "tags" to listOf("kicad")))) {
        dev.stralman.components.layouts.MarkdownLayout {
            org.jetbrains.compose.web.dom.H2(attrs = { id("limitations-in-physical-space") }) {
                org.jetbrains.compose.web.dom.Text("Limitations in physical space..")
                dev.stralman.components.widgets.navigation.HoverLink("#limitations-in-physical-space")
            }
            org.jetbrains.compose.web.dom.P {
                org.jetbrains.compose.web.dom.Text("..or why weirdly shaped PCBs exists. When creating edge cuts for my PCBs I find that the built-in KiCad PCB editor tools to be limited. Sure you can draw lines, arcs, rectangles and circles, which is fine for most simple PCB shapes but for more complicated shapes it's not something I would rely on entirely. Even if I manage to create the shape I want, I still need to find a way to incorporate it into a CAD program to design an enclosure for it. There are ways of exporting KiCad edge cuts into file formats that can then be parsed in other CAD programs. That workflow is flawed when starting with a finished enclosure or specific limitations in dimensions for the PCB. When the PCB needs to adapt to some other shape. Enter DXF graphics support in KiCad!")
            }
            org.jetbrains.compose.web.dom.P {
                org.jetbrains.compose.web.dom.Em {
                    org.jetbrains.compose.web.dom.Text("Disclaimer: I'm not sure when this feature added to KiCad but I know for sure that this works for KiCad 8+")
                }
            }
            org.jetbrains.compose.web.dom.H2(attrs = { id("dxf-and-onshape") }) {
                org.jetbrains.compose.web.dom.Text("DXF and Onshape")
                dev.stralman.components.widgets.navigation.HoverLink("#dxf-and-onshape")
            }
            org.jetbrains.compose.web.dom.P {
                org.jetbrains.compose.web.dom.Text("DXF (or Autocad DXF) is a file format developed by Autodesk for enabling interoperability between CAD programs. ")
                com.varabyte.kobweb.silk.components.navigation.Link("https://www.onshape.com/en/", "Onshape")
                org.jetbrains.compose.web.dom.Text(" is a web based 3D CAD application that I primarily use as an alternative to Autodesk Fusion. I do that simply because there is a free plan that is reasonable for non-commercial use. One of the features that Onshape supports is exporting 2D sketches of what you make e.g. for a weirdly shaped object you draw you can export the projection of the object as an exported DXF file. This file can then be imported into KiCad.")
            }
            org.jetbrains.compose.web.dom.H3(attrs = { id("edge-cut-from-dxf") }) {
                org.jetbrains.compose.web.dom.Text("Edge cut from DXF")
                dev.stralman.components.widgets.navigation.HoverLink("#edge-cut-from-dxf")
            }
            org.jetbrains.compose.web.dom.P {
                org.jetbrains.compose.web.dom.Text("First a sketch needs to be made from a CAD program that supports DXF export.")
            }
            org.jetbrains.compose.web.dom.P {
                com.varabyte.kobweb.silk.components.graphics.Image("/images/dxf_edge_cut_and_zone/1.png", "1.png")
                org.jetbrains.compose.web.dom.Text(" This example uses Onshape. Press the green button to finish sketch. The DXF can then be exported by right-clicking the sketch and pressing \"Export to DXF/DWG\"")
            }
            org.jetbrains.compose.web.dom.P {
                com.varabyte.kobweb.silk.components.graphics.Image("/images/dxf_edge_cut_and_zone/2.png", "2.png")
                org.jetbrains.compose.web.dom.Text(" Open the PCB editor in KiCad and go to ")
                dev.stralman.components.widgets.code.InlineCode("""File > Import > Graphics""")
                org.jetbrains.compose.web.dom.Text(" (or if you are a cool shortcut kiddo press ")
                dev.stralman.components.widgets.code.InlineCode("""Ctrl + Shift + F""")
                org.jetbrains.compose.web.dom.Text(")")
            }
            org.jetbrains.compose.web.dom.P {
                com.varabyte.kobweb.silk.components.graphics.Image("/images/dxf_edge_cut_and_zone/3.png", "3.png")
                org.jetbrains.compose.web.dom.Text(" A prompt will appear. Select the correct units that you used in your CAD program when exporting the DXF. I use millimeters in both Onshape and KiCad (it does not make sense to mix units.. between CAD programs). Then select \"Edge.Cuts\" as layer, press OK.")
            }
            org.jetbrains.compose.web.dom.P {
                com.varabyte.kobweb.silk.components.graphics.Image("/images/dxf_edge_cut_and_zone/4.png", "4.png")
                org.jetbrains.compose.web.dom.Text(" The results should look like this if you use the default colorscheme in the PCB editor. White lines are the color of \"Edge.Cuts\" layer.")
            }
            org.jetbrains.compose.web.dom.P {
                com.varabyte.kobweb.silk.components.graphics.Image("/images/dxf_edge_cut_and_zone/5.png", "5.png")
                org.jetbrains.compose.web.dom.Text(" ..and the 3D model should show you that the edge cut is parsed correctly. Another disclaimer is that maybe not every manufacturer is able to do very complex shapes. Make sure you check this with the manufacturer guidelines before ordering something strange.")
            }
            org.jetbrains.compose.web.dom.H3(attrs = { id("copper-zone-from-dxf") }) {
                org.jetbrains.compose.web.dom.Text("Copper zone from DXF")
                dev.stralman.components.widgets.navigation.HoverLink("#copper-zone-from-dxf")
            }
            org.jetbrains.compose.web.dom.P {
                org.jetbrains.compose.web.dom.Text("In normal cases when I make a copper zone for GND or for VCC I select the ")
                dev.stralman.components.widgets.code.InlineCode("""Add Filled Zone""")
                org.jetbrains.compose.web.dom.Text(" tool (")
                dev.stralman.components.widgets.code.InlineCode("""Ctrl + Shift + Z""")
                org.jetbrains.compose.web.dom.Text("), make a big square that covers the whole PCB then press B to fill all zones and it works just fine.")
            }
            org.jetbrains.compose.web.dom.P {
                com.varabyte.kobweb.silk.components.graphics.Image("/images/dxf_edge_cut_and_zone/6.png", "6.png")
                org.jetbrains.compose.web.dom.Text(" It looks like this.")
            }
            org.jetbrains.compose.web.dom.P {
                org.jetbrains.compose.web.dom.Text("But what if you want the filled zone borders to look more tidy? There is a way to import DXF and then convert its shape into a filled zone. It looks cleaner but other than that there are are not many benefits of doing this.")
            }
            org.jetbrains.compose.web.dom.Ol {
                org.jetbrains.compose.web.dom.Li {
                    org.jetbrains.compose.web.dom.Text("Follow instructions on ")
                    com.varabyte.kobweb.silk.components.navigation.Link("#edge-cut-from-dxf", "how to import DXF as an edge cut")
                    org.jetbrains.compose.web.dom.Text(".")
                }
                org.jetbrains.compose.web.dom.Li {
                    org.jetbrains.compose.web.dom.Text("Set active appearance layer to F.Cu.")
                }
                org.jetbrains.compose.web.dom.Li {
                    dev.stralman.components.widgets.code.InlineCode("""Right-click > Enter group > Right-click > Create from selection > Create zone from Selection""")
                    org.jetbrains.compose.web.dom.Text(", Add Filled Zone prompt should appear")
                }
                org.jetbrains.compose.web.dom.Li {
                    org.jetbrains.compose.web.dom.Text("Un-tick \"Delete source objects after conversion\". Else edge cut needs to be re-added once ok is pressed. (If the shape is too complex the layer KiCad will not add a filled zone and nothing will happen)")
                }
                org.jetbrains.compose.web.dom.Li {
                    org.jetbrains.compose.web.dom.Text("Press B to fill zones.")
                }
            }
            org.jetbrains.compose.web.dom.P {
                com.varabyte.kobweb.silk.components.graphics.Image("/images/dxf_edge_cut_and_zone/7.png", "7.png")
                org.jetbrains.compose.web.dom.Text(" .. and now the edges of the filled zone matches with the edge cut that comes from the DXF.")
            }
            org.jetbrains.compose.web.dom.P {
                org.jetbrains.compose.web.dom.Text("This DXF is actually from a keyboard PCB I'm working on where I derive the copper zone from the DXF and uses it to guide a via stitching addon to follow its edge. An upcoming post will show how that is setup.")
            }
        }
    }
}
